  fileUpload = (e) => {
    let doc = e.target.name;
    let fd = new FormData()
    fd.append("avatar", e.target.files[0])

    axios.post("/image", fd, {
      headers: {
        'Content-Type': "multipart/form-data"
      }
    }).then((res) => {
      let l = "/" + res.data.filename;
      let today = new Date();
      let dd = String(today.getDate()).padStart(2, '0');
      let mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
      let yyyy = today.getFullYear();

      today = dd + '/' + mm + '/' + yyyy;
      let ch=this.state.Check;
      
      if (doc == "PhotoURL") {
        
        ch.PhotoSDate=today;
  
        this.setState({
          PhotoURL: l,
          Check:ch
        })
      }
      if (doc == "AadharURL") {
        ch.AadharSDate=today;
        this.setState({
          AadharURL: l,
          Check:ch
        })
      }
      if (doc == "PanCardURL") {
        ch.PANSDate=today;
        this.setState({
          PanCardURL: l,
          Check:ch
        })
      }
      if (doc == "OwnershipURL") {
        ch.LandSDate=today;
        this.setState({
          OwnershipURL: l,
          Check:ch
        })
      }


    })


  }

